.sky {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #81CFE0;
}

.sky .c1 {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #fff;
}

.sky .c2 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: -50px;
  left: 25px;
  background: #fff;
}

.sky .c3 {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  position: absolute;
  top: -25px;
  left: 100px;
  background: #fff;
}

.sky .c4 {
  width: 110px;
  height: 15px;
  background: #fff;
  position: absolute;
  left: 25px;
  top: 35px;
}

.sky .cloud1 {
  margin-top: 100px;
  position: relative;
}

.sky .cloud2 {
  margin-top: 150px;
  position: relative;
}

.sky .cloud1 {
  -webkit-animation: slide 4s ease-out;
          animation: slide 4s ease-out;
}

.sky .cloud2 {
  -webkit-animation: slide 6s ease-out;
          animation: slide 6s ease-out;
}

@-webkit-keyframes slide {
  from {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes slide {
  from {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}



